export default {
  components: {},
  data() {
    return {
      show_upload_tips: false
    };
  },
  mounted() {
    this.animate_();
  },
  methods: {
    animate_() {
      setTimeout(() => {
        this.show_upload_tips = true;
      }, 3000);
    }
  }
};



